.AppHeader-root {
  padding: 0 8px;
  min-height: 56px !important;
  height: 56px !important;
}

.AppHeader-wrapper::before {
  content: '';
  display: block;
  height: 56px;
}

.AppHeader-menu {
  width: 40px;
  height: 40px;
}

.AppHeader-title {
  text-decoration: none;
  margin-left: 20px;
  padding: 4px;
  font-weight: bold;
  font-size: 16px;
}

.AppHeader-spacer {
  -ms-flex: 1 1;
      flex: 1 1;
}

.AppHeader-actions {
  display: -ms-flexbox;
  display: flex;
}

.AppHeader-search {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  padding-right: 8px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.AppHeader-search-close {
  position: absolute;
  right: 8px;
  top: 0;
  bottom: 0;
  margin: auto;
}

.AppHeader-search-field {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

@media (min-width: 1280px) {
  .AppHeader-root {
    margin-left: 320px;
  }
}


