.AssetBox-root {
  display: inline-grid;
  grid-template: 'x';
  position: relative;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
}

.AssetBox-root > * {
  grid-area: x;
}
.AssetBox-root > :not(.AssetBox-title) {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
}

.AssetBox-title {
  grid-area: x;
  width: 100%;
  height: 100%;
}


