.Popup-root {
  position: relative;
  cursor: pointer;
}

.Popup-popup-root {
  pointer-events: none;
  position: absolute;
  z-index: 1200;
}
.Popup-popup {
  pointer-events: auto;
  position: absolute;
}

.Popup-popup-left {
  right: 0;
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
}
.Popup-popup-right {
  left: 0;
  -webkit-transform-origin: top left;
      -ms-transform-origin: top left;
          transform-origin: top left;
}
.Popup-popup-action {
  right: 0;
  -webkit-transform-origin: top right;
      -ms-transform-origin: top right;
          transform-origin: top right;
}


